<template>
  <div class="container mx-auto" v-if="checklists.length > 0">
    <!-- animation on percentage -->
    <div>
      <h1
        class="my-3 text-xl font-semibold text-center dark:text-white lg:hidden"
      >
        Study Tracker
      </h1>
    </div>

    <div
      class="flex items-center justify-center text-black  totalprogress dark:text-white"
    >
      <circle-progress
        :size="150"
        :percent="totalper > 0 ? totalper : 0"
        :is-gradient="true"
        :is-bg-shadow="true"
        :show-percent="true"
        empty-color="#f7f7f7"
      ></circle-progress>
    </div>

    <div class="flex flex-wrap justify-center gap-3 my-3 dark:text-white">
      <button
        class="px-3 py-2 bg-gray-200 border border-gray-300 border-dashed shadow  dark:border-none dark:border-solid"
        :class="{
          'bg-blue-500 text-white': se == '',
          'dark:bg-red-500': se != '',
        }"
        @click="selectSubject('')"
      >
        All
      </button>
      <button
        v-for="(s, i) in subjects"
        :key="i"
        class="px-3 py-2 bg-gray-200 border border-gray-300 border-dashed shadow  dark:border-none"
        :class="{
          'bg-blue-500 text-white': se == s,
          'dark:bg-red-500': se != s,
        }"
        @click="selectSubject(s)"
      >
        {{ s }}
      </button>
    </div>
    <div class="hidden md:block">
      <pc-routine :checklists="checklists" @selectedItem="openForEdit" />
    </div>
    <div class="md:hidden">
      <mobile-routine :checklists="checklists" @selectedItem="openForEdit" />
    </div>

    <Modal title="Check List Details">
      <template v-slot:body>
        <div class="flex flex-col items-center justify-center space-y-5">
          <div class="flex items-center justify-between w-full px-4">
            <p>Video</p>
            <input
              type="checkbox"
              class="w-5 h-5 text-green-500 rounded form-checkbox"
              v-model="selected.video"
            />
          </div>
          <div class="flex items-center justify-between w-full px-4">
            <p>Book</p>
            <input
              type="checkbox"
              class="w-5 h-5 text-green-500 rounded form-checkbox"
              v-model="selected.book"
            />
          </div>
          <div class="flex items-center justify-between w-full px-4">
            <p>Note</p>
            <input
              type="checkbox"
              class="w-5 h-5 text-green-500 rounded form-checkbox"
              v-model="selected.note"
            />
          </div>
          <div class="flex items-center justify-between w-full px-4">
            <p>Test Paper</p>
            <input
              type="checkbox"
              class="w-5 h-5 text-green-500 rounded form-checkbox"
              v-model="selected.testPaper"
            />
          </div>
          <div class="flex items-center justify-between w-full px-4">
            <p>Question Bank</p>
            <input
              type="checkbox"
              class="w-5 h-5 text-green-500 rounded form-checkbox"
              v-model="selected.qbank"
            />
          </div>
        </div>
      </template>
      <template v-slot:action>
        <div
          class="flex items-center justify-end p-6 border-t border-solid  -b border-blueGray-200"
        >
          <button
            class="px-3 py-3 mr-2 font-semibold text-white bg-red-500  hover:bg-red-600"
            type="button"
            v-on:click="toggleModal()"
          >
            Close
          </button>
          <button
            :disabled="loading"
            class="px-3 py-3 font-semibold text-white bg-green-500  hover:bg-green-600"
            type="button"
            @click="updateitem"
          >
            {{ loading ? "Changing..." : "Save Changes" }}
          </button>
        </div>
      </template>
    </Modal>
  </div>
  <Loader v-else />
</template>

<script>
import { mapState, mapMutations } from "vuex";
import PcRoutine from "@/components/PcRoutine";
import MobileRoutine from "@/components/MobileRoutine";
import Modal from "@/components/Modal";
import Loader from "@/components/Loader";
import CircleProgress from "vue3-circle-progress";

export default {
  components: {
    PcRoutine,
    Modal,
    MobileRoutine,
    Loader,
    CircleProgress,
  },
  data() {
    return {
      checklists: [],
      allchecklists: [],
      checkitem: false,

      selected: {},
      loading: false,
      totalper: 0,
      se: "",
      subjects: new Set(),
    };
  },
  methods: {
    ...mapMutations(["toggleModal"]),
    updateitem() {
      this.loading = true;
      fetch(
        "https://script.google.com/macros/s/AKfycbzesOKX8-ma3N26RfY3vvMoeeORSk1eX2RlKaUJsPkzR3vzcfDavZDgwZtLA77ihmi1Qg/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            sheetid: this.user.displayName,
            ...this.selected,
            type: "progress",
          }),
        }
      ).then(() => {
        let index = this.checklists.findIndex((s) => s.id == this.selected.id);

        this.checklists[index] = { ...this.selected };
        this.loading = false;
        this.$store.commit("toggleModal");
        this.getChecklists();
        this.checkitem = false;
      });
    },
    openForEdit(item) {
      this.selected = item;
      this.toggleModal();
    },
    getChecklists() {
      fetch(
        `https://script.google.com/macros/s/AKfycbzFgqSH8kZIIIKtH6C7P7TzEMfJgXoiNP2VdCZf7n_hLxyq3OLFMEUtuBSgIY3agt7i/exec?sheet=${this.user.displayName}&type=progress`
      )
        .then((res) => res.json())
        .then((data) => {
          fetch(
            "https://script.google.com/macros/s/AKfycbym77m6CPMbha4InMiUZDoV6fMEC2ili7jX-iDVWh_vS0De8Hh0wHVM9bvHiB6LFWIy/exec"
          )
            .then((r) => r.json())
            .then((k) => {
              let pers = 0;
              this.allchecklists = data.routines.map((d) => {
                let check = k.routine.find((m) => d.id == m.id);
                if (d.Subject) {
                  let s = d.Subject.split(/\d/)[0];
                  this.subjects.add(s);
                }
                let c = this.getProgress(d);
                pers += c["progressper"];
                c.playlist = check?.playlist;
                return c;
              });

              this.allchecklists.length = 75;
              this.checklists = this.allchecklists;
              this.totalper = pers / this.checklists.length;
            });
        });
    },
    getProgress(data) {
      let count = 0;
      for (const k in data) {
        if (["video", "book", "note", "qbank", "testPaper"].includes(k)) {
          if (data[k]) {
            count++;
          }
        }
      }

      switch (count) {
        case 1:
          data["progress"] = "More to Go";
          break;
        case 2:
          data["progress"] = "Study Hard";
          break;
        case 3:
          data["progress"] = "Keep it up";
          break;
        case 4:
          data["progress"] = "Almost There";
          break;
        case 5:
          data["progress"] = "Chapter Complete";
          break;
        default:
          data["progress"] = "No progress";
      }

      data["progressper"] = count * 20;
      return data;
    },
    selectSubject(s) {
      this.se = s;
      this.checklists = this.allchecklists.filter((r) => {
        if (r.Subject) {
          if (r.Subject.toLowerCase().includes(s.toLowerCase())) {
            return r;
          }
        }
      });
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  created() {
    this.getChecklists();
  },
};
</script>

<style lang="scss" scoped>
.table {
  border-spacing: 0 15px;
}
</style>